.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.on {
    padding: 10px 10px;
    background-color: rgb(48, 177, 43);
}

.off{
    padding: 10px 10px;
    background-color: rgb(187, 21, 19);
}

#card{
    display: block;
    height: 300px;
    width: 400px;
    background: rgb(188, 237, 33);
    perspective: 1300px;
    margin: 50px;
    transition: 0.2s;
}
